      //         <p>Pse je nejprve teba vytvoit (vytvo pse) a pot se d vybrat v editoru a editovat. Mnit se me jmno, interpret a hlavn text a akordy samotn. Dleit jsou pro ns i autoi hudby a autoi textu, take pokud je zn, urit nm pomohou.</p>
      //         <p></p>
      //         <hr />
